import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { API_URL } from './config';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Injectable()
export class MoneyService {
  constructor(private _http: Http) { }

  getMoneyHistory(id: number): Observable<any> {
    const url = `${API_URL}/moneyhis/${id}`;
    return this._http.get(url).map((response: Response) => response.json());
  }

  // getMoneyHistoryTable(id: number): Observable<any> {
  //   const url = `${API_URL}/moneyhis/${id}`;
  //   return this._http.get(url).map((response: Response) => response);
  // }

  getMoneyInfo(id: number): Observable<any> {
    const url = `${API_URL}/UserMoney/${id}`;
    return this._http.get(url).map((response: Response) => response.json());
  }

  AddTransRes(data: any) {
    const url = `${API_URL}/money/trans`;
    return this._http.post(url, data).map((response: Response) => response.json());
  }

  AddWithdrawRes(data: any) {
    const url = `${API_URL}/money/withdraw`;
    return this._http.post(url, data).map((response: Response) => response.json());
  }

  GetListTransSuccess(sendUsername: string) {
    const url = `${API_URL}/trans-success/${sendUsername}`;
    return this._http.get(url).map((response: Response) => response.json());
  }

  GetListTransFail(sendUsername: string) {
    const url = `${API_URL}/trans-fail/${sendUsername}`;
    return this._http.get(url).map((response: Response) => response.json());
  }

  GetListRutChuaXN(userID: number) {
    const url = `${API_URL}/rut-chuaxn/${userID}`;
    return this._http.get(url).map((response: Response) => response.json());
  }

  GetListRutDaXN(userID: number) {
    const url = `${API_URL}/rut-daxn/${userID}`;
    return this._http.get(url).map((response: Response) => response.json());
  }
  GetListRutDaHuy(userID: number) {
    const url = `${API_URL}/rut-dahuy/${userID}`;
    return this._http.get(url).map((response: Response) => response.json());
  }
}



