import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-mxv-spin',
  templateUrl: './mxv-spin.component.html',
  styleUrls: ['./mxv-spin.component.scss']
})
export class MxvSpinComponent implements OnInit {
  @Input() value: string = "";
  @Input() step: number = 1;
  @Input() min: number = 0;
  @Input() max: number = 0;
  @Output() onChange = new EventEmitter<number>();
  constructor() { }

  ngOnInit() {
  }

  up():void {
    let _value = +this.value;
    if (_value + this.step <= this.max || this.max < this.min) {
      this.value = (_value + this.step).toString();
      this.onChange.emit(+this.value);
    }
  }

  down(): void {
    let _value = +this.value;
    if (_value - this.step >= this.min) {
      this.value = (_value - this.step).toString();
      this.onChange.emit(+this.value);
    }
  }
}



