import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { API_URL } from './config';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';

@Injectable()
export class OrderService {
    constructor(private _http: Http) { }

    getListViTheMo(id: number): Observable<any> {
        const url = `${API_URL}/vithemo/${id}`;
        return this._http.get(url).map((response: Response) => response.json());
    }

    getListViTheDong(id: number): Observable<any> {
        const url = `${API_URL}/vithedong/${id}`;
        return this._http.get(url).map((response: Response) => response.json());
    }

    getListLenhTrongPhien(id: number): Observable<any> {
        const url = `${API_URL}/lenhtrongphien/${id}`;
        return this._http.get(url).map((response: Response) => response.json());
    }

    getListLenhCho(id: number): Observable<any> {
        const url = `${API_URL}/lenhcho/${id}`;
        return this._http.get(url).map((response: Response) => response.json());
    }

    getListLenhDaHuy(id: number): Observable<any> {
        const url = `${API_URL}/lenhdahuy/${id}`;
        return this._http.get(url).map((response: Response) => response.json());
    }

    Add(data: any): Observable<any> {
        const url = `${API_URL}/order/`;
        return this._http.post(url, data).map((response: Response) => response);
    }

    Update(data: any): Observable<any> {
        const url = `${API_URL}/order/`;
        return this._http.put(url, data).map((response: Response) => response);
    }

    Delete(id: number): Observable<any> {
        const url = `${API_URL}/order/${id}`;
        return this._http.delete(url).map((response: Response) => response);
    }
}


